import React, { useState } from 'react';

// Componentes
import Card from '../generales/Card';
import Info from './info.json';
import '../generales/modoD-L.css';

const Principal = ()=> {

    const [ color, setColor ] = useState(false);

    const cambiarColor = ()=> {
        color ? setColor(false) : setColor(true);
    }

    return(
        <div className={ color ? "backColorLight" : "backColorDark"}>
            <h1 className="text-center py-4">Replicas - Front End</h1>
            <p className="text-center bg-success py-3">Aqui se muestra una replica de algunos sitios populares. El cual fue desarrollado con React</p>
            <div className="d-flex row justify-content-center pb-5">
            {
                Info.map(e=>
                            <Card key={e.titulo}
                                  cardTitle={e.titulo}
                                  estilo={ color ? "card mt-3 mx-3 shadow" : 
                                  "card mt-3 mx-3 bg-dark text-white border-success"}
                                  styleBtn={ color ? "btn btn-outline-dark mb-3" : 
                                  "btn btn-outline-success mb-3"}
                                  cardLink={e.enlace}
                                  cardDescription={e.descripcion}
                                  cardButton={e.boton}
                                  cardImg={e.img}/>
                        )
            }
            </div>
            <div className={ color ? "menu bg-dark text-white shadow" : "menu bg-light text-dark"}>
                <h2 className="btnMenu">+</h2>
                {
                    color ? 
                <button onClick={cambiarColor} className={ color ? "btnOnLight mb-3" : "btnOnDark mb-3" }>Normal</button>
                :
                <button onClick={cambiarColor} className={ color ? "btnOnLight mb-3" : "btnOnDark mb-3"}>Oscuro</button>
                }
            </div>
           
            
        </div>
    );
}



export default Principal;