import React from 'react';

// Componentes 
import CardYT from './CardYT';
import Info from './info.json';

const Main = ()=> {
    return(
        <div className="bg-light col-md-10 col-12 row pt-4 px-5">
            {
                Info[0].main.map((e, index)=> 
                    <div key={index} className="col-md-4 col-xl-3 col-12 ">
                        <CardYT img={e.img}
                                logo={e.logo}
                                titulo={e.titulo}
                                canal={e.canal}
                                vistas={e.vistas}
                                publicado={e.publicado}
                                />
                    </div>
                )
            }
            
        </div>
    );
}
export default Main;