import React, { useState } from 'react';

import Info from './info.json';

const NavGoogle = ()=> {
    
    const [ mostrar, setMostrar ] = useState(false);
    const [ menu, setMenu ] = useState(false);

    const mostrarCard = (event)=> {
        if(event){
            if(mostrar){
                setMostrar(false);
            } else {
                setMostrar(true);
                setMenu(false);
            }
        }
    }
    const mostrarMenu = (event)=> {
        if(event){
            if(menu){
                setMenu(false);
            } else {
                setMenu(true);
                setMostrar(false);
            }
        }
    }

    return(
        <nav className="d-flex justify-content-end mt-2 px-3">
                <div className="d-flex mt-3">
                    <label className="mx-3">Gmail</label>
                    <label>Imagenes</label>
                    <button className="btn" onClick={mostrarMenu}>
                        <svg focusable="false" width="30px" 
                            style={{position: "relative", top: -6, height: 40}} className="d-flex">
                            <path d="M6,8c1.1,0 2,-0.9 2,-2s-0.9,-2 -2,-2 -2,0.9 -2,2 0.9,2 2,2zM12,20c1.1,0 2,-0.9 2,-2s-0.9,-2 -2,-2 -2,0.9 -2,2 0.9,2 2,2zM6,20c1.1,0 2,-0.9 2,-2s-0.9,-2 -2,-2 -2,0.9 -2,2 0.9,2 2,2zM6,14c1.1,0 2,-0.9 2,-2s-0.9,-2 -2,-2 -2,0.9 -2,2 0.9,2 2,2zM12,14c1.1,0 2,-0.9 2,-2s-0.9,-2 -2,-2 -2,0.9 -2,2 0.9,2 2,2zM16,6c0,1.1 0.9,2 2,2s2,-0.9 2,-2 -0.9,-2 -2,-2 -2,0.9 -2,2zM12,8c1.1,0 2,-0.9 2,-2s-0.9,-2 -2,-2 -2,0.9 -2,2 0.9,2 2,2zM18,14c1.1,0 2,-0.9 2,-2s-0.9,-2 -2,-2 -2,0.9 -2,2 0.9,2 2,2zM18,20c1.1,0 2,-0.9 2,-2s-0.9,-2 -2,-2 -2,0.9 -2,2 0.9,2 2,2z"></path>
                        </svg>
                    </button>
                </div>
                <div>
                    <button className="btn" onClick={mostrarCard}>
                        <img className="imgGoogleTiny m-auto mb-3" alt="img perfil" 
                        src="https://1078328784.rsc.cdn77.org/mobilewallpaper/thumbnail/The-Day-you-Left-512.jpg"/>
                    </button>
                </div>
                {/* Perfil de Google */}
                {
                    mostrar && 
                    <div className="card text-center pt-3 pb-3 shadow bordesCard">
                    <img className="imgGoogle m-auto mb-3" alt="img perfil"
                    src="https://1078328784.rsc.cdn77.org/mobilewallpaper/thumbnail/The-Day-you-Left-512.jpg"/>
                    <h5 className="px-5">rodrigo quinteros <br/>
                    <p className="font-gris font-size">roodrigoquinteros@gmail.com</p>
                    </h5>
                    <div className="justify-content-center">
                        <button className="btn border col-8">Gestionar tu cuenta de Google</button>
                    </div>
                    <hr className="font-gris"/>
                    <div className="d-flex justify-content-center">
                        <img className="imgGoogleCard" src="https://image.flaticon.com/icons/png/512/359/359858.png" alt="img google" />
                        <h6 className="mt-1 mx-2">Añadir otra cuenta</h6>
                    </div>
                        <hr className="font-gris"/>
                    <div className="justify-content-center">
                        <button className="btn border col-4">Cerrar sesión</button>
                    </div>
                    <hr className="font-gris"/>
                    <label className="font-gris font-size px-5">Política de Privacidad | Términos del Servicio</label>
                </div>
                }

                {
                    menu &&
                    <div className="row border text-center shadow 
                                    anchoCardGoogle overflow-auto bg-light">
                    {
                        Info.map((e, index)=> 
                            <div key={index} className="col-4 pt-3 miniCardMenu">
                                <img alt={e.titulo} src={e.img}
                                className="imgGoogleTiny mx-1" />
                                <p>{e.titulo}</p>
                            </div>)
                    }
                    </div>
                }
                

            </nav>
    );
}
export default NavGoogle;