import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';

const Card = (props)=> {
    return(
        <Fragment>
            <div className={props.estilo} style={{width: '18rem'}}>
                <h5 className="card-title mt-3 text-center">{props.cardTitle}</h5>
                <img src={props.cardImg} className="card-img-top mb-2" alt="..." />
                <p className="card-text text-center">{props.cardDescription}</p>
                <Link to={props.cardLink} 
                    className={props.styleBtn}>{props.cardButton}
                </Link>
            </div>
        </Fragment>
    );
}
export default Card;