import React, { useState, Fragment } from 'react';

// Componentes
import VolverBtn from '../generales/VolverBtn';
import MainForm from './nuevo/MainFormNuevo';
import Footer from './nuevo/FooterNuevo';
import Principal from './Principal';
import FacebookActual from './viejo/FacebookViejo';
import './facebookStyled.css';

const Facebook = ()=> {

    const [ facebookViejo, setFacebookViejo ] = useState(false);
    const [ facebookActual, setFacebookActual ] = useState(false);
    const [ menuFacebook, setMenuFacebook ] = useState(false);
    const [ screenPrincipal, setScreenPrincipal ] = useState(true);

    const addFacebookActual = (event)=> {
        event && facebookViejo ? setFacebookViejo(false) 
        : setFacebookViejo(true); setFacebookActual(false); setScreenPrincipal(false) ; setMenuFacebook(false); 
    }
    const addFacebookViejo = (event)=> {
        event && facebookActual ? setFacebookActual(false) 
        : setFacebookActual(true); setFacebookViejo(false); setScreenPrincipal(false); setMenuFacebook(false); 
    }
    const addMenuFacebook = (event)=> {
        event && menuFacebook ? setMenuFacebook(false) 
        : setMenuFacebook(true); 
    }
    const addScreenPrincipal = (event)=> {
        event && screenPrincipal ? setScreenPrincipal(false) 
        : setScreenPrincipal(true); setFacebookActual(false); setFacebookViejo(false);
    }


    return(
        <div className="bg-light">
            <VolverBtn resumen="Replica de Facebook con la opcion de alternar entre el Front del 2010 y el del 2021"/>
            {
                screenPrincipal &&
                <Fragment>
                    <Principal faceViejo={addFacebookViejo} faceActual={addFacebookActual} />
                </Fragment>
            }
           
            {
                facebookViejo || facebookActual ?
                    <div>
                        <button className="btn btn-info text-white position-absolute end-0 m-2 px-3 " onClick={addMenuFacebook}>Menu</button>
                        {
                            menuFacebook &&
                                <div className="menu-contenedor shadow">
                                    {
                                        facebookActual ? 
                                        <>
                                            <button onClick={addFacebookActual} className="btn text-white w-100 btn-info">Ver Facebook Actual</button>
                                        </>
                                        : 
                                        <>
                                            <button onClick={addFacebookActual} className="btn text-white w-100 btn-info disabled">
                                                Ver Facebook Actual <label className="text-danger">(off)</label></button>
                                        </>
                                    }
                                    {
                                        facebookViejo ? 
                                        <>
                                            <button onClick={addFacebookViejo} className="btn text-white w-100 btn-info mt-3 mb-3">Ver Facebook Viejo</button>
                                        </>
                                        : 
                                        <>
                                            <button onClick={addFacebookViejo} className="btn text-white w-100 btn-info mb-3 mt-3 disabled">
                                                Ver Facebook Viejo <label className="text-danger">(off)</label></button>
                                        </>
                                    }
                                    <button onClick={addScreenPrincipal} className="btn text-white w-100 btn-info">Pantalla principal</button>            
                                </div>
                        }
                        
                    </div>
                    :
                    <div></div>
                    }
                        {   
                            facebookViejo &&
                            <div>
                                <MainForm/>
                                <div className="pt-md-5 mt-md-5">
                                    <Footer/>
                                </div>
                            </div>
                        }
                        {
                            facebookActual &&
                                <div>
                                    <FacebookActual/>
                                </div>
                        }
        </div>
    );
}

export default Facebook;