import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import '../../../node_modules/bootstrap/dist/js/bootstrap.bundle';

const VolverBtn = (props)=> {

    const [ cerrar, setCerrar ] = useState(false);

    const cerrarNav = (event)=> {
        setCerrar(true);
    }

    return(
        <div>
            {
                !cerrar &&
                <div className="d-flex d-fixed justify-content-end bg-dark p-2">
                <div>
                    <button type="button" className="btn btn-success" data-bs-toggle="modal" data-bs-target="#exampleModal">
                    Resumen
                    </button>
                    <div className="modal fade" id="exampleModal" tabIndex={-1} aria-labelledby="exampleModalLabel" aria-hidden="true">
                        <div className="modal-dialog">
                            <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel">Regresar</h5>
                                <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
                            </div>
                            <div className="modal-body">
                                {props.resumen} 
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-dark" data-bs-dismiss="modal">Entendido</button>
                            </div>
                            </div>
                        </div>
                    </div>
                </div>
                <button className="btn btn-success mx-4">
                    <Link className="text-white" to="/">Volver</Link>
                </button>
                <button className="btn btn-outline-success px-3" onClick={cerrarNav}>x</button>
            </div>
            }
        </div>
    );
}
export default VolverBtn;