import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import React, { Fragment } from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';

// Componentes
import Principal from './componentes/principal/Principal';
import EdTeam from './componentes/edteam/EdTeam';
import Google from './componentes/google/Google';
import Facebook from './componentes/facebook/Facebook';
import YouTube from './componentes/youtube/YouTube';

function App() {
  return (
    <BrowserRouter>
      <Switch>
        <Fragment>
          <Route path="/" exact component={Principal} />
          <Route path="/youtube" exact component={YouTube} />
          <Route path="/edTeam" exact component={EdTeam} />
          <Route path="/google" exact component={Google} />
          <Route path="/facebook" exact component={Facebook} />
        </Fragment>
      </Switch>
    </BrowserRouter>
  );
}

export default App;
