import React, { useState } from 'react';

// Componentes
import SideBarMovil from './SideBarMovil';
import SideBarDesk from './SideBarDesk';
import VolverBtn from '../generales/VolverBtn';
import Main from './Main';
import NavBar from './NavBar';

const YouTube = ()=> {

    const [ mostrarSide, setMostrarSide ] = useState(true);

    const addSide = (event)=> {
        event && mostrarSide ? setMostrarSide(false) : setMostrarSide(true);
    }

    return(
        <div>
            <VolverBtn/>
            <NavBar mostrarSide={addSide}/>
            <SideBarMovil/>
            <hr className="mt-md-0 mt-2 text-secondary"/>

            <div className="d-flex justify-content-center">
                {
                    mostrarSide &&
                        <SideBarDesk/>
                }
                <Main/>
            </div>
        </div>
    );
}

export default YouTube;